.navBar {
    position: fixed;
    width: 100%;
    background: #a11813;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #240800, #a11813); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    height: 56px;
    top: 0;
    left: 0;
    box-shadow: 0px 5px 4px rgba(0, 0, 0, 0.5);
}

.navBar__navigation {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.navBar__control {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;   
}
.navBar__logo a{
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    margin-left: 1rem;
    text-transform: uppercase;
}

.navBar__navigation-item-normal {
    font-family: "Nunito";
    transition:all 0.5s linear;
    color:#efefef;
    text-decoration:none;
    text-transform:uppercase;
    font-weight:200;
    font-style: normal;
    padding-bottom:5px;
    background: linear-gradient(to right,rgb(255, 255, 255) 0%,rgb(255, 255, 255) 100%);
    background-size:0px 0px;
    background-repeat: no-repeat;
    background-position:50% 85%;
    text-shadow:0;
    font-size: 1rem;
}

.navBar__navigation-items a:hover
{
    background-size:100% 1px;
    font-weight:200;
    text-shadow:0 2.5px 4px rgba(0,0,0,0.5);
}

.navBar__navigation-item-active{
    background-size:100% 1px;
    font-weight:200;
    text-shadow:0 2.5px 4px rgba(0,0,0,0.5);
}



.navBar__navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
}

.navBar__navigation-items li {
    padding: 0 1.5rem;
}

.navBar__socials{
    font-size: 1.5rem;
}

@media (max-width: 930px){
    .navBar__navigation-items{
        display: none;
    }
}
@media (min-width: 930px){
    .navBar__button{
        display: none;
    }

    .navBar__logo{
        margin-left: 0;
    }
}
.toggle-button {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 20px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button-line {
    width: 30px;
    height: 2px;
    background: white;
}


.side-drawer {
    height: 100%;
    background-color:#2d2f2f;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-120%);
            transform: translateX(-120%);
    transition: -webkit-transform 0.2s ease-out;
    transition: transform 0.2s ease-out;
    transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

.side-drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 80%;
}

.side-drawer li {
    padding: 1.0rem 0;
}


@media (min-width: 930px){
    .side-drawer{
        display: none;
    }
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 100;
}
.tournaments_parallax {
    /* The image used */
    background-image: url("https://beerponghannover.de/wp-content/uploads/2017/11/hanomacke_wandlogo.png");
  
    /* Set a specific height */
    min-height: 50%; 
  
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .tournament_content {
      padding: 0 1rem;
      max-width: 800px;
      margin: auto;
  }

  .tournament_content_flex {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
  }

  hr {
      border: 1px solid #313536;
  }
.tournamentList {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}

.tournamentName {
    float: left;
}

.tournamentDate {
    float:right;
}

.clear {
    clear: both;
}

.tournamentContent {
    overflow: hidden;
    transition: max-height 0.5s ease-out;
}
